.page-body {
  width: 60%;
  margin: 0 auto;
}

nav {
  border-bottom: 2px solid rgb(214, 213, 213);
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0;
}

nav ul li a {
  text-decoration: none;
  padding: 1rem;
  font-weight: 500;
}

nav ul li a:hover {
  background-color: #222;
  color: #fff;
}

.article-list-item {
  border-bottom: 1px solid darkgray;
}

.article-list-item a {
  text-decoration: none;
}

.other-articles h2 {
  margin-top: 2rem;
  text-transform: capitalize;
}

.other-articles a {
  text-decoration: none;
}

.upvotes {
  color: royalblue;
}

@media screen and (max-width: 767px) {
  .page-body {
    width: 95%;
  }
}
